<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/restaurant/add-location')">Add Location</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="label">
        <el-input placeholder="label" v-model="param.label"></el-input>
      </el-form-item>
      <el-form-item label="xcoord">
        <el-input placeholder="xcoord" v-model="param.xCoord"></el-input>
      </el-form-item>
      <el-form-item label="ycoord">
        <el-input placeholder="ycoord" v-model="param.yCoord"></el-input>
      </el-form-item>
      <el-form-item label="space">
        <el-input placeholder="space" v-model="param.space"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="label"
          prop="label"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="xcoord"
          prop="xcoord"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="ycoord"
          prop="ycoord"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="space"
          prop="space"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        label: '',
        xCoord: '',
        yCoord: '',
        space: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/locations/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>

